import React, { useState } from 'react'
import Web3 from "web3";

function App() {

    const [connected, setConnected] = useState(false)
    const [wallet, setWallet] = useState(null)
    //const [web3, setWeb3] = useState(null)

    const connectWallet = async () => {
        if (window.ethereum) {
            const web3 = new Web3(window.ethereum)
            await window.ethereum.enable()
            let account = await web3.eth.getAccounts()
            setWallet(account)
        }
    }

    return (
    <div className="bg-gray-600 h-screen">
        <div className="flex bg-white h-24">
            <p className='flex-none text-3xl rounded-2xl mx-10 my-8'> WyanToken </p>
            <div className='flex-grow'></div>
            {wallet == null && <button
                className='flex-none rounded-3xl h-16 w-60 my-4 bg-blue-300 text-white text-lg p-3 border-8 border-white'
                onClick={connectWallet}
            > Connect Wallet </button> }
            {wallet != null && <p className='flex-none p-6 my-3 mx-10 max-w-xs truncate rounded-3xl bg-blue-300 text-white text-lg'>{wallet}</p>}

        </div>
    </div>
    );
}

export default App;
